
import { defineComponent } from 'vue';
import { useRouter } from 'vue-router';
export default defineComponent({
  setup() {
    const router = useRouter();
    const payEnd = () => {
      router.push({
        path: './buy',
        query: {
          isGet: 'true'
        }
      });
    };
    const payAgain = () => {
      location.href = sessionStorage.getItem('mweb_url') as string;
    };
    const payBack = () => {
      router.push('/home');
    };
    return {
      payEnd,
      payAgain,
      payBack
    };
  }
});
